@mixin text-small() {
    font-size: 0.875rem; // 14px
    line-height: 143%;
    letter-spacing: 0.018em;
    font-weight: 500;
}

@mixin text-medium() {
    font-size: 1rem; // 16px
    line-height: 150%;
    letter-spacing: 0.0275em;
    font-weight: 500;
}

@mixin text-large() {
    font-size: 1.25rem; // 20px
    line-height: 158%;
    letter-spacing: 0.0075em;
}

// BP message boxes

// BP message boxes
// Creates a message box with border, background color
// If  no text-color passed in then the background used for
// text is darkened by 50%.
@mixin message-box($background: #fff, $text-color: null, $border: null) {

    @if $text-color {
        // if a param was passed through
        $text-color: $text-color;
    } @else {
        $text-color: darken($background, 50%);
    }

    background: $background;

    @if $border != none {

        @if $border {
            border: $border;
        } @else {
            border: 1px solid darken($background, 10%);
        }
    }

    color: $text-color;
}

// Essential em based breakpoints
// mixin wraps rulesets or properties.
// written as:
// @include medium-up() {
//  body {property: value;}
// }


@mixin small-up {

    @media screen and (min-width: 384px) { // Default 24em

        @content;
    }
}

@mixin medium-small-max {

    @media screen and (max-width: 767px) { // Default 32em

        @content;
    }
}

@mixin medium-small-up {

    @media screen and (min-width: 768px) { // Default 32em

        @content;
    }
}

@mixin medium-max {

    @media screen and (max-width: 1024px) { // Default 46.8em

        @content;
    }
}

@mixin medium-up {

    @media screen and (min-width: 1025px) { // Default 46.8em

        @content;
    }
}

@mixin medium-lrg-up {

    @media screen and (min-width: 1200px) { // Default 55em

        @content;
    }
}

@mixin large-up {

    @media screen and (min-width: 1400px) { // Default 75em

        @content;
    }
}



